<template>
  <van-tag round :color="getBackgroundColor">
    {{ name }}
  </van-tag>
</template>
<script>
export default {
  name: 'alreadyEntered',
  data() {
    return {
      name: '未录入',
      getBackgroundColor: ''
    }
  },
  props: {
    tagDto: {
      type: Object,
      default: () => {}
    }
  },
  watch: {
    tagDto: {
      handler() {
        this.name=this.tagDto.name;
        this.getBackgroundColor=this.tagDto.setBackgroundColor
      },
      deep: true
    }
  }
}
</script>
