<template>
  <div class="vehicleSurvey-content-taskConclusion" @click="entryConclusion">
    <div>
      <span class="title">
        任务结论
      </span>
      <already-entered :tagDto="tagDto"></already-entered>
    </div>
    <div class="tip">
      任务结论书写。
    </div>
  </div>
</template>
<script>
import alreadyEntered from '@/components/vehicleSurvey/alreadyEntered'
export default {
  name: 'taskConclusion',
  components: {
    'already-entered': alreadyEntered,
  },
  data() {
    return {
      tagDto: {
        name: '未录入',
        setBackgroundColor: '#7d7e80'
      }
    }
  },
  methods: {
    entryConclusion() {
      this.tagDto.name='已录入';
      this.tagDto.setBackgroundColor='rgb(243,120,54)'
    }
  }
}
</script>
<style scoped lang="scss">
.tip{
  padding: 10px 0;
  font-size: 14px;
  color: $gray;
}
</style>
