<template>
  <div class="vehicleSurvey-content-lossItem">
    <div @click="addLossItem">
      <span class="title">
        损失项信息
      </span>
      <span class="font_orange">
        添加损失项
      </span>
    </div>
    <div class="tip">
      点击添加可添加损失信息。
    </div>
    <ul class="already-add-lossItem">
      <li class="item" @click="entryLossItem">
        <div>
          <i class="iconfont icon-shanjian"></i>
          <span>
            车辆-沪A9876【标】
          </span>
          <already-entered :tagDto="tagDto"></already-entered>
        </div>
        <i class="iconfont icon-tiaozhuan"></i>
      </li>
    </ul>
    <van-popup v-model="show" position="bottom">
      <ul class="choose-content">
        <li class="choose-item" v-for="(item,index) in chooseItem" :key="index">
          {{ item }}
        </li>
      </ul>
      <div class="choose-cancel" @click="cancelLossItem">
        取消
      </div>
    </van-popup>
  </div>
</template>
<script>
import alreadyEntered from '@/components/vehicleSurvey/alreadyEntered'
export default {
  name: 'lossItemMessage',
  components: {
    'already-entered': alreadyEntered,
  },
  data() {
    return {
      chooseItem: ['添加车辆信息','添加财产信息','添加伤者信息'],
      tagDto: {
        name: '未录入',
        setBackgroundColor: '#7d7e80'
      },
      show: false
    }
  },
  methods: {
    //添加损失项
    addLossItem(){
      this.show=true;
    },
    //取消损失项
    cancelLossItem() {
      this.show=false;
    },
    //录入损失项
    entryLossItem() {
      this.tagDto.name='已录入';
      this.tagDto.setBackgroundColor='rgb(243,120,54)'
    },

  }
}
</script>
<style scoped lang="scss">
.van-popup{
  background-color: rgba(0,0,0,0);
}
.van-popup--bottom{
  width: 90%;
  margin-bottom: 15px;
}
.font_orange{
  color: $orange-dark;
}
.tip{
  padding: 10px 0;
  font-size: 14px;
  color: $gray;
}
.vehicleSurvey-content-lossItem{
  &>div:first-child{
    display: flex;
    justify-content: space-between;
  }
  .already-add-lossItem{
    height: 50px;
    line-height: 50px;
    border-top: 1px solid $gray;
    .item{
      display: flex;
      justify-content: space-between;
    }
  }
}
.choose-content{
  width: 100%;
  background: $white;
  margin: 0 auto;
  border-radius: 5px;
  .choose-item{
    width: 100%;
    height: 50px;
    line-height: 50px;
    text-align: center;
    &:not(:last-child){
      border-bottom: 1px solid $gray;
    }
  }
}
.choose-cancel{
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: $orange-dark;
  margin-top: 8px;
  background-color: $white;
  border-radius: 5px;
}
</style>
