<template>
  <div class="vehicleSurvey-main">
    <c-nav-bar class="vehicleSurvey-header">
      <span slot="left" class="nav-bar-left">
        <i class="iconfont icon-fanhui1"></i>
      </span>
      <span slot="center" class="nav-bar-center">
        勘验任务
      </span>
      <span slot="right" class="nav-bar-right">
        分享位置
      </span>
    </c-nav-bar>
    <div class="vehicleSurvey-content" ref="content">
      <div class="vehicleSurvey-content-warn">
        <div class="title">
          注意事项
        </div>
        <ul class="content">
          <li v-for="(item,index) in warningData" :key="index">
            {{ index+1 }}、{{ item }}
          </li>
        </ul>
      </div>
      <vehicle-survey-message></vehicle-survey-message>
      <loss-item-message></loss-item-message>
      <task-conclusion></task-conclusion>
    </div>
  </div>
</template>
<script>
import vehicleSurveyMessage from '@/components/vehicleSurvey/vehicleSurveyMessage'
import lossItemMessage from '@/components/vehicleSurvey/lossItemMessage'
import taskConclusion from '@/components/vehicleSurvey/taskConclusion'
import BScroll from 'better-scroll'
export default {
  name: 'vehicleSurvey',
  components: {
    'vehicle-survey-message': vehicleSurveyMessage,
    'loss-item-message': lossItemMessage,
    'task-conclusion': taskConclusion
  },
  data() {
    return {
      warningData: [
        '注意事项第一条。',
        '请认真填写以下内容。',
        '类目与页面提供的内容不一致将会审核失败。'
      ]
    }
  },
  mounted() {
    setTimeout(() => {
      this.scroll = new BScroll(this.$refs.content,{
        probeType: 1,
        click: true,
        eventPassthrough: 'horizontal'
      })
    },20)
  },
}
</script>
<style scoped lang="scss">
.title{
  font-size: 16px;
  font-weight: bold;
}
.vehicleSurvey-content{
  width:100%;
  top:44px;
  position:absolute;
  z-index:10;
  bottom:49px;
  &>div:not(:first-child){
    width: 100%;
    margin-top: 10px;
    padding: 20px 20px 20px 15px;
    background: $white;
  }
  .vehicleSurvey-content-warn{
    width: 100%;
    height: 140px;
    background: linear-gradient(to right,$orange-dark,$orange);
    color: $white;
    font-size: 14px;
    .title{
      padding-top: 19px;
      padding-left: 18px;
    }
    .content{
      padding-left: 15px;
      li{
        padding-top: 7px;
      }
    }
  }

}
</style>
