<template>
  <div class="vehicle-survey-message-main">
    <div class="vehicleSurvey-content-message" @click="entryMessage">
      <div class="title">
        勘验信息
        <already-entered :tagDto="tagDto"></already-entered>
      </div>
      <p class="content">
        点击录入勘验信息
      </p>
    </div>
  </div>
</template>
<script>
import alreadyEntered from '@/components/vehicleSurvey/alreadyEntered'
export default {
  name: 'vehicleSurveyMessage',
  components: {
    'already-entered': alreadyEntered,
  },
  data() {
    return {
      chooseItem: ['添加车辆信息','添加财产信息','添加伤者信息'],
      tagDto: {
        name: '未录入',
        setBackgroundColor: '#7d7e80'
      }
    }
  },
  methods: {
    entryMessage() {
      this.tagDto.name='已录入';
      this.tagDto.setBackgroundColor='rgb(243,120,54)';
      this.$router.push({name: '进度一'})
    },
  }
}
</script>
<style scoped lang="scss">
.vehicleSurvey-content-message{
  font-size: 14px;
  .content{
    margin-top: 10px;
    line-height: 22px;
    letter-spacing: 1px;
  }
}
</style>
